<template>

</template>

<script>
  export default {
    data() {
      return {

      }
    },
    created() {
      this.$store.dispatch('getBaseInfo').then((res) => {
        let access = [];
        if (res.operations && res.operations.length > 0) {
          res.operations.map((item) => {
            access.push(item.code);
          })
        }

        if (access.indexOf('sr_storeroom_homepage') > -1) {
          this.toMenu('/home');
        } else if (access.indexOf('sr_personalApply') > -1) {
          this.toMenu('/applyGoods');
        } else if (access.indexOf('sr_personalAudit') > -1) {
          this.toMenu('/department');
        } else if (access.indexOf('sr_warehouseManage') > -1) {
          this.toMenu('/goodsProvide');
        } else if (access.indexOf('sr_statistics') > -1) {
          this.toMenu('/goodsStatistics');
        } else if (access.indexOf('sr_goods') > -1) {
          this.toMenu('/warehouseFiles');
        }
      });
    },
    methods: {
      //路由跳转
      toMenu(path) {
        this.$router.replace({path:path});
      },
    },
    mounted() {

    }
  }
</script>

<style scoped>

</style>



